const initPartnersSlider = () => {
  const swiper01 = document.querySelector('.work-with-us__slider');
  if (swiper01) {
    new Swiper(swiper01, {
      // Optional parameters

      //       
      loop: true,
      loopedSlides: 6,
      autoplay: {
        delay: 0,
        disableOnInteraction: false,
      },
      speed: 3500,
      freeMode: true,
      watchOverflow: true,
      slidesPerView: 'auto',
      slidesPerGroup: 1,
      spaceBetween: 0,
    });
  }

};
export {initPartnersSlider};

